<template>
    <div class="container">
        <div class="level-container" v-if="level===0">
            <div class="level-title">难度选择</div>
            <div class="level-item" @click="ChangeLevel(1)">初级</div>
            <div class="level-item" @click="ChangeLevel(2)">中级</div>
            <div class="level-item" @click="ChangeLevel(3)">高级</div>
        </div>
        <div class="mine-container" v-else>
            <minesweeper :level="level" @reset="reset" v-if="!initbuf">
                <div id="choice" @click="ChangeLevel(0)">选择难度</div>
            </minesweeper>
        </div>
    </div>
</template>

<script>
import minesweeper from '@/components/minesweeper';
export default {
    data(){
        return{
            level: 0,
            initbuf: false
        }
    },
    methods:{
        ChangeLevel(x = 0){
            this.level = x;
            // console.log(this.level);
            // console.log(this);
            // console.log(event);
            // console.log(event.path[0].innerText = '123');
        },
        reset(buff){
            this.initbuf = buff
            this.$nextTick(
                function () {
                    this.initbuf = false
                }
            )
        }
    },
    mounted:function(){

    },
    components:{
        minesweeper,
    }
}
</script>

<style lang="scss" scoped>
.level-container{
    margin: 30% 0;
    width: 100%;
}
.level-title{
    font-size: 20px;
    width: 40%;
    margin: 40px auto;
    text-align: center;
}
.level-item{
    width: 40%;
    height: 10vw;
    margin: 10vw auto;
    text-align: center;
    line-height: 10vw;
    border: 1px solid black;

    
}

</style>


