<template>
    <div class="g-container">
            <div v-if="start" >
                <Level></Level>
            </div>
            <div v-else>开始游戏</div>
    </div>
</template>

<script>
import Level from "@/components/level";
export default {
    data(){
        return{
            start: true
        }
    },
    components:{
        Level,
    }
}
</script>

<style>
.g-container{
    height: 100%;
}
</style>


