<template>
  <swiper class="swiper" :options="swiperOption">
    <swiper-slide>
      <slot></slot>
    </swiper-slide>
    <!-- <div class="swiper-scrollbar" slot="scrollbar"></div> -->
  </swiper>
</template>

<script>
  import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
  import 'swiper/css/swiper.css'
  export default {
    name: 'test',
    title: 'Scroll container',
    components: {
      Swiper,
      SwiperSlide
    },
    data() {
      return {
        swiperOption: {
          direction: 'vertical',
          slidesPerView: 'auto',
          freeMode: true,
          scrollbar: {
            // el: '.swiper-scrollbar',
            // hide:true
          },
          mousewheel: true
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  .swiper {
    height: 100vh;
    padding-bottom: 10vh;
    /deep/ .swiper-slide {
      height: auto;
    }
  }
</style>


